@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

#home {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: #997a6f81;
    width: 60%;
    min-width: 440px;
    height: 100%;
    border-radius: 0px 0px 0px 0px;
}


#conteudoHome {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

#textoHome {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60%;
    height: max-content;
    font-family: 'Roboto Condensed', sans-serif;
    color: black;
    background-color: rgba(255, 255, 255, 0.315);
    border-radius: 10px;
    margin: 5%;
}

#textoHome > h1 {
    font-size: 2rem;
    font-family: 'Roboto Condensed', sans-serif;
    color: black;
    text-align: center;
}

#textoHome > p {
    font-size: 1rem;
    margin: 5%;
}

#cowboy {
    box-sizing: border-box;
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#cowboy > img {
    margin: 5%;
    width: 80%;
}

@media (max-width: 1300px) {
    #home {
        width: 80%;
    }
}

@media (max-width: 768px) {

    #textoHome > h1 {
        font-size: 1.5rem;
    }

    #textoHome > p {
        font-size: 0.9rem;
        margin: 5%;
    }
}