@import url('https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap');

#alerta {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #dd18185e;
    width: 100%;
    min-width: 440px;
    height: 70px;
}

.conteudoAlerta {
    width: 90%;
}

.conteudoAlerta > p {
    font-size: 80%;
    text-align: center;
    font-family: 'Lato', sans-serif;
    color: white;
}

@media (max-width: 425px) {
    #alerta {
        height: 90px;
    }
    .conteudoAlerta {
        height: 90px;
    }
    .conteudoAlerta > p {
        font-size: 70%;
    }
}