@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

#scroll {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    min-width: 440px;
    border-radius: 0px 0px 0px 0px;
}

#containerHistoria {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: rgba(255, 255, 255, 0.795);
    background-color: #997a6f81;
    width: 60%;
    min-width: 440px;
    border-radius: 0px 0px 0px 0px;
}

.linhas {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}

.textoHistoria {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    background-color: rgba(255, 255, 255, 0.349);
    padding: 2%;
    margin: 1%;
    border-radius: 10px;
}

.textoHistoria > h1 {
    text-align: center;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.5rem;
    text-shadow: 1px 1px 2px black;
    color: black;
}
.textoHistoria > p {
    text-align: left;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 0.8rem;
    color: black;
}

.fotosHistoria {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.fotosHistoria > img {
    width: 50%;
    border-radius: 10px;
}


@media (max-width: 1300px) {
    #containerHistoria {
      width: 80%;
    }
}

@media (max-width: 720px) {
    #containerHistoria .linhas {
        display: flex;
        flex-direction: column;
    }

    #containerHistoria .textoHistoria {
        width: 90%;
    }
}