@import url('https://fonts.googleapis.com/css2?family=Anek+Telugu:wght@500&display=swap');

#footer {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgba(255, 255, 255, 0.795);
    background-color: #24201f9a;
    width: 60%;
    min-width: 440px;
    height: max-content;
    border-radius: 0px 0px 30px 30px;
}

#guardaItens {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 2%;
}

#links {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}

#links > img {
    width: 30px;
}

#textolink {
    box-sizing: border-box;
    margin: 2%;
    font-family: 'Anek Telugu', sans-serif;
}

a {
    font-size: 1rem;
    font-family: 'Anek Telugu', sans-serif;
    padding: 1%;
    text-align: center;
}

a {
    color: white;
}

@media (max-width: 1300px) {
    #footer {
        width: 80%;
    }

    a {
        margin: 2%;
        font-size: 0.8rem;
    }

    #links > img {
        width: 20px;
    }
    
}

@media (max-width: 768px) {
    a {
        margin: 2%;
        font-size: 0.6rem;
    }
    
}

@media (max-width: 425px) {
    a {
        margin: 2%;
        font-size: 0.8rem;
    }

    #footer {
        display: flex;
        flex-direction: column;
        border-radius: 0px;
        width: 100vw;
    }

    #guardaItens {
        
    }

    #links {
        width: 50%;
    }

    #textolink {
        width: 50%;
    }

    #links > img {
        justify-content: flex-start;
        width: 30px;
        margin: 2%;
    }
}