#corpo {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: #997a6f81;
    width: 60%;
    min-width: 440px;
    height: max-content;
    border-radius: 0px 0px 0px 0px;
}


#container {
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;  /* horizontal */
    align-items: center;            /* vertical */ 
    flex-wrap: wrap;                /* quebra de linha para caber no container */
    align-content: center;          /* juntando as duas linhas no centro */
    width: 100%;
    min-width: 440px;
    height: max-content;
    border-radius: 0px 0px 0px 0px;
}

#guardaFrame {
    box-sizing: border-box;
    display: flex;
    justify-content: center; /* horizontal */
    align-items: center; /* vertical */
    flex-wrap: wrap;
    width: 25%;
    height: 48%;
    padding: 10px;
}

#iframe {
    box-shadow: 2px 2px 5px 1px;;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    min-width: 100px;
}

.paginas {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    list-style: none; /* remove o "icone" antes do nr na lista */
    background-color: #f7ecd680;
    border-radius: 0px 0px 0px 0px;
}

/* modificando os BotÃµes */
.button {
    width: 30px;
    height: 30px;
    font-size: 0.8rem;
    font-weight: bold;
    margin: 5px;
    border: 2px solid #9B7C59;
    border-radius: 10px;
    color: black;
    background-color: white;
    transition-duration: 0.6s;
    }
    
.button:hover {
    background-color: #704b2d;
    color: white;
    }

    @media (max-width: 1300px) {
        #corpo {
          width: 80%;
        }
    }

    @media (max-width: 500px) {
        #container {
            flex-wrap: nowrap;
            flex-direction: column;
        }
        #guardaFrame {
            width: 60%;
            height: 50%;
        }
    }