@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');

.container {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
}

#menu-section {
    box-sizing: border-box;
    background-color: #835F52;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
}

#linkContainer {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

#linkContainer.active {
    display: flex;
}

#botaoMenu {
    box-sizing: border-box;
    background-color: #835f5200;
    border: none;
    height: 50px;
    font-size: 12px;
    font-family: 'Roboto Condensed', sans-serif;
    padding: 0px 20px 0px 20px;
    font-weight: bold;
    transition-duration: 0.6s;
    color: rgb(211, 198, 179);
}

#botaoMenu:hover {
    background-color: #704b2d;
    color: white;
    cursor: pointer;
    }

#btn-menu {
    display: none;
    font-family: 'Staatliches', cursive;
    padding: .5rem 1rem;
    font-size: 1.2rem;
    border: none;
    background: none;
    cursor: pointer;
    gap: .5rem;
}

#hamburguer {
    color: rgb(0, 0, 0);
    width: 30px;
    border-top: 3px solid;
}
#hamburguer::after, #hamburguer::before {
    display: block;
    content: '';
    width: 30px;
    height: 3px;
    background: currentColor;
    margin-top: 5px;
    transition: .5s;
    position: relative;

}

#menu-section.active #hamburguer {
    border-top-color: transparent;
}

#menu-section.active #hamburguer::before {
    transform: rotate(135deg);
}

#menu-section.active #hamburguer::after {
    transform: rotate(-135deg);
    top: -7px;
}

a:link, a:visited {
    text-align: center;
    text-decoration: none;
    display: inline-block;
}

@media (max-width: 768px) {

    #linkContainer {
        display: block;
        position: absolute;
        width: 80%;
        top: 25%;
        left: 10%;
        height: 0px;
        background-color: #835f52e0;
        border-radius: 0px 0px 10px 10px;
        z-index: 1000;
        transition: 0.6s;
        overflow-y: hidden;
    }
    #linkContainer.active {
        display: flex;
        flex-direction: column;
        height: 400px;
        overflow-y: hidden;
    }
    #botaoMenu {
        font-size: 20px;
        background-color: #835f52;
        display: none;
    }

    #botaoMenu.active {
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        border-radius: 10px;
        background-color: #835f5200;
    }

    #botaoMenu.active:hover {
        background-color: #704b2d;
        color: white;
        cursor: pointer;
    }
    #btn-menu {
        display: flex;
    }

}

@media (max-width: 425px) {
    #botaoMenu {
        font-size: 7px;
    }
}