@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

#scroll {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    min-width: 440px;
    border-radius: 0px 0px 0px 0px;
}

#containerMusica {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #997a6f81;
    width: 60%;
    min-width: 440px;
    border-radius: 0px 0px 0px 0px;
}

.linhas {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 95%;
}

.textoMusica {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 70%;
    min-width: 50%;
    background-color: rgba(255, 255, 255, 0.349);
    padding: 2%;
    margin: 1%;
    border-radius: 10px;
}

.textoMusica > h1 {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 2rem;
    text-shadow: 1px 1px 2px black;
    color: black;
}
.textoMusica > p {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1rem;
    color: black;
}

.fotosMusica {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.fotosMusica > img {
    width: 60%;
    border-radius: 10px;
    margin: 5px;
}

@media (max-width: 1300px) {
    #containerMusica {
      width: 80%;
    }
}

@media (max-width: 720px) {
    #containerMusica .linhas {
        display: flex;
        flex-direction: column-reverse;
    }

    #containerHistoria .textoHistoria {
        width: 90%;
    }
}