@import url('https://fonts.googleapis.com/css2?family=Bangers&display=swap');

#header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #9e8f766b;
    width: 60%;
    min-width: 440px;
    height: 150px;
    min-height: 150px;
    border-radius: 30px 30px 0px 0px;
  }
  
  #guardaTitulo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  #titulo {
    color: #3a0909e0;
    font-family: 'Bangers', cursive;
    font-variant: small-caps;
    font-size: 4.5rem;
  }

  @media (max-width: 1300px) {
    #header {
      width: 80%;
    }
    
    #titulo {
      font-size: 3.5rem;
    }
  }

  @media (max-width: 768px) {
    #titulo {
      font-size: 2.8rem;
    }
  }

  @media (max-width: 425px) {
    #header {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #9e8f766b;
      width: 100%;
      height: 150px;
      border-radius: 0px;
    }
  }